* {
  font-family: 'Montserrat', 'Rubik', sans-serif;
  color: #393c41;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  width: 100%;
  overflow-x:hidden ;
}

a {
  text-decoration: none;
}

li{
  list-style: none;
}

@keyframes animateDown {
  0%, 20% ,50%,80%,100% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(5px);
  }

  60% {
    transform: translateY(3px);
  }
}